* {margin: 0; padding: 0;}

/* 헤더 */
.App {
  background: rgb(247, 247, 176);
}

.App > h1 {
  color: rgb(49, 21, 126);
  text-align: center;
  font-size: 20px;
  line-height: 55px;
}

header {
  height: 60px;
  position: fixed;
  width: 100%;
  top: 0px; left: 0px;
  box-shadow: 0px 0px 10px #969696;
  z-index: 1001;
}


.bars_btn, .close_btn, .mnulist_btn {
  border: none;
  background: transparent;
  color: #333;
  cursor: pointer;
}
.bars_btn, .close_btn {
  font-size: 24px;
}
.bars_btn {
  border: none;
  font-size: 24px;
  background: transparent;
  position: fixed;
  top: 15px;
  right: 20px;
  color: #333;
  cursor: pointer;
}
.close_btn {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px;
}
.mnulist_btn {
  font-size: 16px;
  padding: 8px;
  width: 100%;
  text-align: left;
}

.mnubox {padding: 12px;}
.mnubox ul {margin: 0;}
.mnubox li {
  list-style: none;
  margin: 0;
}
.mnubox li:hover button {
  text-decoration: underline;
  color: rgb(21, 21, 119);
}

.mnu_bg {
  width: 100%;
  background: rgba(0, 0, 0, 0.78);
  height: 100vh;
}

/* 메인 */
main {margin: 2px 5px 16px 5px; padding-top: 60px;}

.title3 {
  color: rgb(21, 21, 119);
  margin: 15px auto;
  background-color: rgb(145, 199, 145);
  text-indent: 10px;
}
h3 {
  margin: 7px;
  border-bottom: 2px solid #aaa;
  padding-bottom: 3px;
}
ul, li, ol {
  margin: 2px 12px;
  padding: 2px;
}
p {
  margin: 0px 12px;
  padding: 5px 0;
}
pre {
  font-size: 14px;
  padding: 8px;
  border: 1px solid #ddd;
  margin: 6px auto;
  width: 98%;
  text-wrap: wrap;
}
dl {
  margin: 2px 5px;
}
dt {
  font-weight: bold;
}
dd {
  text-indent: 5px;
}

h4 {
  margin: 7px;
  padding: 3px 0;
  background: #f5f5f5;
}

.act {
  color: rgb(110, 28, 110);
  margin-top: 35px;
}
img {max-width: 100%;}

input, label {
  margin: 2px 4px;
  padding: 2px;
}

/* 푸터 */
.footer {
  background: #333;
  color: #fff;
  text-align: center;
  line-height: 40px;
}